<template>
    <div class="moblieContent">
        <header>
             <img class="logo" src="@/assets/img/moblie/moblielogo.png" alt="logo">
             <div class="slotContent">
                 <h3 class="info">
                     彩票app下載 
                     <span class="redBg">官方</span>
                 </h3>
                 <!-- <img class="slot" src="@/assets/img/moblie/slot.png" alt="logo"> -->
             </div>
             <div class="headInfo">
                <p class="mr0">·此專案由vue.js開發的app下載平台·</p>
                <h3>【下載官网：{{siteUrl}} 】</h3>
                <template v-if="deviceType == 'wx'|| deviceType == 'qq'">
                     <div  class="wxInfo orangeRadius">
                        微信/QQ内无法下载应用
                    </div>
                </template>
             </div>
             <template v-if="deviceType == 'ios'">
                <div class="downloadInfoBlock shadow" :style="{'margin-bottom':'35px'}">
                    <div class="mr0 lineTitle">安装提示</div>
                    <p>App助手是一款为了协助彩世界苹果用户遇系统签名掉签，系统更新或软件无法使用时的下载安装工具。建议用户下载。</p>
                    <!-- <a :href="downloadHelperlink" class="orangeRadius downloadhelperBtn">
                        安装彩世界助手
                    </a> -->
                    <router-link to="/helper" class="orangeRadius downloadhelperBtn">安装App助手</router-link>
                </div>
             </template>
        </header>
        <div class="lotteryListContent">
            <div v-for="(items, index) in lotteryArray" :key="index" class="dlBlock" @click="moveTo(items.lotteryName)">
                <div class="blockHeader">
                    <img  :src="require(`@/assets/img/`+items.lotteryName+`Logo.png`)" alt="logo">
                    <!-- <img  :src="require(`@/assets/img/liuheLogo.png`)" alt="logo"> -->
                    <div class="appInfo inlBlock">
                        <h3 :class="(items.lotteryName == 'liuhe')&&'official'">{{items.title}}</h3>
                        <p>{{items.appInfo}}</p>
                    </div>
                    <button class="btn redBg" @click.stop="downloadApp(items.lotteryName)">
                        下载
                    </button>
                    <!-- <router-link :to="{path:'/lotteryDetail',query:{pathId:items.lotteryName}}" class="btn redBg">
                        下载
                    </router-link> -->
                </div>
                <div class="blockInfo">
                    {{infoFilter(items.info)}}
                </div>
            </div>
            <p>icon by <a target="_blank" href="https://icons8.com">Icons8</a></p>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex';
export default {
    data(){
        return {
            siteUrl:"",
            downloadHelperlink:""
        }
    },
    computed:{
        ...mapState({
            deviceType:'deviceType',
            lotteryArray:'lotteryArray'
        }),
    },
    mounted(){
        this.downloadHelperlink = window.downloadHelper;
         var _this = this;
        setTimeout(function(){
            _this.siteUrl = window.siteUrl;
        },500);
    },
    methods:{
        infoFilter(info){
            let flitercontent = info.split("");
            let output = "";
            //console.log('flitercontent',flitercontent);
            if(flitercontent.length > 30){
                output = info.slice(0,30) + '...';
            }else{
                output = info;
            }
            return output;
        },
        moveTo(pathname){
            // this.$store.commit('sendCurrent','lotterydetail')
            // this.$store.commit('sendPathName',pathname);

            // this.$router.push({
            //     name:"lotteryDetail",
            //     query:{
            //         pathId:pathname
            //     }
            // })
            window.location.href = './detail/'+pathname+'.html';
        },
        downloadApp(pathname){
            let dwArray = window.download;
            for(let i=0;i<dwArray.length;i++){
                if(pathname == dwArray[i].name){
                    if(this.deviceType == 'ios'){
                        //  window.location.href = './detail/'+dwArray[i].name+'.html?iosdownload=true';
                         alert('這是ios裝置，本平台無下載只為展示用');
                    }else if(this.deviceType == 'android'){
                        // window.location.href = dwArray[i].androidLink;
                         alert('這是android裝置，本平台無下載只為展示用');
                    }
                }
            }
        }
    }
}
</script>

